import * as React from 'react';
import Header from './Header';
import { Box } from 'grommet';
import BottomNavBar from './BottomNavBar';
import './App.css'
import CurrentPage from './containers/CurrentPage/CurrentPage';
import VKConnection from './VKApi/VKConnection';

export default class App extends React.Component {
  public render() {
    VKConnection.Init(error => {});
    VKConnection.PayToTestGroup(2, () => {}, error => {}, "Woops!!!!");

    return (
      <div>
        <Header />
          { VKConnection.GetEmail(email => { throw alert(email)}, error => {})}
        <div className='main'>
          <Box fill='vertical' background={{
            "color": "neutral-1",
            "opacity": 'weak'
          }}>
            <CurrentPage />
          </Box>
        </div>
        <BottomNavBar />
      </div>
    );
  }
}


